.what {
    width: 20em;
    padding: 5%;
}

.errorDiv {
    position: absolute;
    left: 50%;
    top: 10%;
    transform: translate(-50%, -10%);
    white-space: pre-wrap;
    width: 80%;
}


.errorDiv > div > h1 {
    font-size: 3em;
    font-weight: 400;
}

.errorDiv > div > h3 {
    font-size: 1.5em;
    font-weight: 300;
}

.errorDiv > div {
    transform: translate(0, 100%);
}

.errorDiv > img {
    float: left;
}

.errorDiv > * {
    transition: .4s;
}

@media (max-width: 800px) {
    .errorDiv > img {
        float: none;
    }

    .errorDiv > div {
        transform: translate(0% , 0%);
    }
}

