body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  transition: .5s;
}

.paddingTop40 {
  padding-top: 40px;
}

.paddingTop30 {
  padding-top: 30px;
}

.paddingTop20 {
  padding-top: 20px;
}

.paddingLeft20 {
  padding-left: 20px;
}

.paddingTop2em {
  padding-top: 2em;
}

.wtc {
  color: white;
}

.gtc {
  color: gray;
}

.lgtc {
  color: lightgray;
}

.center {
  text-align: center;
  align-items: center;
}

.center > div {
  display: inline;
}

.BigTitleName {
  font-size: 12vw;
  font-weight: 500;
}

.SmallTitleName {
  font-size: 9vw;
  font-weight: 200;
}

.Description {
  font-size: 3.2vw;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.724);
  background-color: rgba(115, 115, 115, 0.269);
  padding: 5px;
  border-radius: 10px;
}

@media (min-width: 768px) {
  .BigTitleName {
      font-size: 9vw;
      font-weight: 500;
  }
  
  .SmallTitleName {
      font-size: 6vw;
      font-weight: 200;
  }

  .Description {
      font-size: 2.4vw;
      font-weight: 500;
  }
}

.menuA {
  color: white;
}

.borderBottom2px {
  border-bottom: 2px solid rgba(171, 171, 171, 0.787);
}

.sectionTitle {
  padding-left: 0.8em;
  color: #828282;
  font-size: 2em;
  font-weight: 300;
}

.Dever {
  width: 50%;
  margin-left: 20%;
  margin-right: 20%;
  margin-bottom: 10%;
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 1%;
  padding-bottom: 1%;
  height: 30%;
  display: inline-block;
}

.DeverImage {
  width: 30%;
  float: left;
}

.DeverImage > img {
  border-radius: 100%;
  border: 2px solid #bbbbbb;
  width: 100%;
}

.DeverTextInfo {
  width: 65%;
  padding-top: 5%;
  padding-bottom: 5%;
  height: 90%;
  float: left;
  padding-left: 5%;
}

.DeverName {
  font-size: 1.1em;
  font-weight: 400;
  color: #828282;
}

.DeverName > a {
  text-decoration: none;
  font-size: 0.5em;
}

.DeverInfo {
  font-size: 1.2em;
  font-weight: 400;
  color: #828282;
  padding-left: 5%;
}

.weightper {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
}

.bgfbfbfb {
  background-color: #fbfbfb;
}

.notbpd {
  padding-top: 0px;
  padding-bottom: 0px;
}

.sectionTitle {
  padding-top: 20px;
  margin-top: 0px;
}

.Project {
  width: 100%;
  padding-top: 1%;
  padding-bottom: 1%;
  height: 100%;
  margin-bottom: 15%;
  display: inline-block;
}

.ProjectTextInfo {
  width: 90%;
  height: 90%;
  float: inline-start;
  white-space: pre-line;
  margin-left: 5%;
  margin-right: 5%;
}

.ProjectName {
  font-size: 1.1em;
  font-weight: 400;
  color: #828282;
  white-space: pre-line;
  float: inline-start;
  width: 100%;
}

.ProjectInfo {
  font-size: 1.2em;
  font-weight: 400;
  color: #828282;
  width: 95%;
  white-space: pre-line;
  padding-left: 5%;
}

.Project {
  width: 70%;
  margin-left: 15%;
}

@media (max-width: 500px) {
  .ProjectName , .DeverInfo {
    font-size: 1em;
  }

  .ProjectInfo , .DeverInfo {
    font-size: 0.8em;
  }
}

.HeaderTitle {
  padding-top: 10vh;
}

.WhiteHeader {
  background-color: #0000008e;
}

.Header {
  position: fixed;
  width: 100%;
  top: 0px;
  display: grid;
  grid-template-areas: "logo nav";
  padding-top: 15px;
}

.Nav {
  grid-area: nav;
  display: grid;
  grid-template-columns: repeat(4 , auto);
  align-items: center;
  justify-items: center;
}

/* Logo ImageSize */

.logo {
  width: 60px;
}

.menuA {
  text-decoration: none;
  color: black;
  font-size: 0.6rem;
  font-weight: lighter;
}

@media (min-width: 300px){
  .logo {
      width: 63px;
  }

  .menuA {
      font-size: 1em;
  }
}

@media (min-width: 400px){
  .logo {
      width: 70px;
  }

  .menuA {
      font-size: 1.2em;
  }
}

@media (min-width: 500px){
  .logo {
      width: 80px;
  }

  .menuA {
      font-size: 1.4em;
  }
}

@media (min-width: 600px){
  .logo {
      width: 110px;
  }

  .menuA {
      font-size: 1.7em;
  }
}

@media (min-width: 769px){
  .logo {
      width: 130px;
  }

  .menuA {
      font-size: 2em;
  }
}

.headerSubMenu > p {
  padding: 0;
  margin: 0;
  width: 0;
  height: 0;
}

.menuA {
  color: lightgray;
}

.headerMenu {
  position: relative;
  display: inline-block;
}

.headerSubMenu {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.headerSubMenu a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  font-size: 1em;
}

.headerSubMenu a:hover {background-color: #ddd;}

.headerMenu:hover .headerSubMenu {display: block;}

.Dever > a > .DeverTextInfo > .DeverName {
  color: blueviolet;
}